import { Navigate, useNavigate, useParams } from "react-router-dom";
import { useState, useEffect, useRef } from "react";
import Select from "react-select";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const EditPackageLayer = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const centreId = localStorage.getItem("centreId");
  const [loading, setLoading] = useState(false);

  const [packageData, setPackageData] = useState({
    name: "",
    actualPrice: "",
    discountPrice: "",
    totalPrice: "",
    doctor: [],
    description: "",
    shiftTimes: [{ day: "", from: "", to: "" }],
    reportingTime: "",
    platformFee: "",
    selectedServices: [],
  });

  const [doctors, setDoctors] = useState([]);
  const [services, setServices] = useState([]);
  const [status, setStatus] = useState("");

  // Fetch package details based on ID from URL
  useEffect(() => {
    const fetchPackageDetails = async () => {
      try {
        setLoading(true);
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/packages/single/${id}`
        );

        const packageDetails = response.data.package || response.data;

        // Convert time format from 12-hour to 24-hour for input fields
        const convertTo24Hour = (time12h) => {
          if (!time12h) return "";

          const [time, modifier] = time12h.split(" ");
          let [hours, minutes] = time.split(":");

          if (hours === "12") {
            hours = "00";
          }

          if (modifier === "PM") {
            hours = parseInt(hours, 10) + 12;
          }

          return `${hours}:${minutes}`;
        };

        // Format the shift times
        const formattedShiftTimes = packageDetails.slots
          ? packageDetails.slots.map((slot) => ({
              day: slot.day || "",
              from: convertTo24Hour(slot.from) || "",
              to: convertTo24Hour(slot.to) || "",
            }))
          : [{ day: "", from: "", to: "" }];

        // Set the package data with the fetched details
        setPackageData({
          name: packageDetails.packageName || "",
          actualPrice: packageDetails.actualPrice || "",
          discountPrice: packageDetails.discountPrice || "",
          totalPrice: packageDetails.totalPrice || "",
          doctor: packageDetails.doctors
            ? packageDetails.doctors.map((d) => d._id)
            : [],
          description: packageDetails.description || "",
          shiftTimes: formattedShiftTimes,
          reportingTime: packageDetails.reportTiming || "",
          platformFee: packageDetails.platformFees || "",
          selectedServices: packageDetails.services
            ? packageDetails.services.map((service) => ({
                value: service._id,
                label: `${service.serviceName} (${
                  service?.serviceCategory?.category_name || "Unknown Category"
                })`,
              }))
            : [],
        });
        console.log("Package details",packageDetails);
        
      } catch (error) {
        console.error("Error fetching package details:", error);
        toast.error("Failed to load package details");
      } finally {
        setLoading(false);
      }
    };

    if (id) {
      fetchPackageDetails();
    }
  }, [id]);

  // Fetch centre status
  useEffect(() => {
    const fetchCentreDetails = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/centres/${centreId}`
        );
        setStatus(res.data.status);
      } catch (error) {
        console.error("Error fetching centre details", error.message);
      }
    };

    if (centreId) {
      fetchCentreDetails();
    }
  }, [centreId]);

  // Fetch doctors
  useEffect(() => {
    const fetchDoctors = async () => {
      if (!centreId) {
        console.error("Centre ID not found in localStorage");
        return;
      }
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/doctors/centre/${centreId}`
        );
        setDoctors(res.data);
      } catch (error) {
        console.error("Error fetching doctors:", error);
      }
    };

    if (centreId) {
      fetchDoctors();
    }
  }, [centreId]);

  // Fetch services
  useEffect(() => {
    const fetchServices = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/getAllServices`
        );
       
                 console.log("Services name",res.data.services);

        const formatted = res.data.services.map((service) => ({
          
          label: `${service.serviceName} (${
            service?.serviceCategory?.category_name 
          })`,
          value: service._id,
        }));
        setServices(formatted);
        console.log("Form",formatted);
        
      } catch (error) {
        console.error("Error fetching services:", error);
      }
    };

    fetchServices();
  }, []);

  // Doctor options
  const doctorOptions = [
    { value: "add_doctor", label: "Add New Doctor" },
    ...doctors.map((doc) => ({
      value: doc._id,
      label: doc.name,
    })),
  ];

  const selectedDoctorOptions = doctorOptions.filter((opt) =>
    packageData.doctor.includes(opt.value)
  );

  const handleDoctorChange = (selected) => {
    if (!selected) {
      setPackageData((prev) => ({
        ...prev,
        doctor: [],
      }));
      return;
    }

    const selectedValues = selected.map((opt) => opt.value);

    if (selectedValues.includes("add_doctor")) {
      navigate("/create-doctor");
      return;
    }

    setPackageData((prev) => ({
      ...prev,
      doctor: selected.map((opt) => opt.value),
    }));
  };

  const handleShiftTimeChange = (index, field, value) => {
    const updated = [...packageData.shiftTimes];
    updated[index][field] = value;
    setPackageData((prev) => ({
      ...prev,
      shiftTimes: updated,
    }));
  };

  const removeShiftTime = (index) => {
    const updated = packageData.shiftTimes.filter((_, i) => i !== index);
    setPackageData((prev) => ({
      ...prev,
      shiftTimes: updated,
    }));
  };

  const addShiftTime = () => {
    setPackageData((prev) => ({
      ...prev,
      shiftTimes: [...prev.shiftTimes, { day: "", from: "", to: "" }],
    }));
  };

  const convertTo12Hour = (time) => {
    if (!time) return "";

    const [hour, minute] = time.split(":");
    const date = new Date();
    date.setHours(hour, minute, 0);

    return date.toLocaleTimeString("en-US", {
      hour: "2-digit",
      minute: "2-digit",
      hour12: true,
    });
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setPackageData((prev) => ({ ...prev, [name]: value }));
  };

const handleChange = (e) => {
  const { name, value, type } = e.target;

  // ð¨ Step 1: Check if user entered prices before selecting services
  if (["actualPrice", "discountPrice", "totalPrice"].includes(name)) {
    if (!packageData.selectedServices || packageData.selectedServices.length === 0) {
      alert("Please select at least one service before entering prices!");
      return; // Stop further execution
    }
  }

  // ð¯ Step 2: Convert the value properly
  const newValue = type === "number" ? (value === "" ? "" : Number(value)) : value;

  // ð§  Step 3: Update the package data and recalculate totals
  setPackageData((prev) => {
    let updated = {
      ...prev,
      [name]: newValue,
    };

    // Fetch latest values
    const actual =
      name === "actualPrice" ? Number(newValue) : Number(updated.actualPrice);
    const discount =
      name === "discountPrice" ? Number(newValue) : Number(updated.discountPrice);
    const fees = Number(updated.platformFee) || 0;

    // â Step 4: Apply price + fee logic
    if (!isNaN(discount) && discount > 0) {
      // Case 1: Discount entered
      const total = discount + fees;

      // Validation â total cannot exceed or equal actual
      if (actual && actual <= total) {
        alert(
          "Actual price can't be less than or equal to total price (â¹" + total + ")"
        );
        updated.discountPrice = "";
        updated.totalPrice = actual + fees; // revert to actual-based total
      } else {
        updated.totalPrice = total;
      }
    } else if (!isNaN(actual) && actual > 0) {
      // Case 2: Discount removed or empty
      updated.totalPrice = actual + fees;
    } else {
      // Case 3: Both empty
      updated.totalPrice = "";
    }

    return updated;
  });
};


  const handleServiceChange = (selectedOptions) => {
    const updatedServices = selectedOptions || [];

    const actual = Number(packageData.actualPrice || 0);
    const discount = Number(packageData.discountPrice || 0);

    const {
      adjustedActualPrice,
      adjustedDiscountPrice,
      totalPrice,
      platformFee,
    } = calculateTotalPrice(actual, discount, updatedServices);

    setPackageData((prev) => ({
      ...prev,
      selectedServices: updatedServices,
      totalPrice: totalPrice,
      platformFee: platformFee,
    }));
  };

    const [confirmModal,setConfirmModal] = useState(false);
    
   const modalRef = useRef(null);
    
        useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
             confirmModal
            ) {
              confirmModal(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // ð Scroll lock logic
          if (confirmModal) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [confirmModal]);

  const handleSubmit = (e) => {
  e.preventDefault();
  if (status !== "Approved") {
    toast.error(
      "Please Complete your profile details and wait for admin approval to start listing your services."
    );
    return;
  }

  setConfirmModal(true);
};

  const confirmSaveService = async (e) => {
     setConfirmModal(false);

    const payload = {
      packageName: packageData.name,
      actualPrice: Number(packageData.actualPrice),
      discountPrice: Number(packageData.discountPrice || 0),
      totalPrice: Number(packageData.totalPrice),
      doctors: packageData.doctor,
      platformFees: Number(packageData.platformFee),
      slots: packageData.shiftTimes.map((slot) => ({
        day: slot.day,
        from: convertTo12Hour(slot.from),
        to: convertTo12Hour(slot.to),
      })),
      description: packageData.description,
      services: packageData.selectedServices.map((s) => s.value),
      reportTiming: packageData.reportingTime,
      centreId: centreId,
    };


    try {
      const response = await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/packages/${id}`,
        payload
      );

      if (response.status === 200 || response.status === 201) {
        toast.success(
          response?.data?.message || "Package updated successfully!"
        );
        setTimeout(() => {
          navigate("/package");
        }, 3000);
      } else {
        alert("Something went wrong while updating the package.");
      }
    } catch (error) {
      console.error("Error updating package:", error);
      toast.error(error.response?.data?.message || "Failed to update package");
    }
  };

  const calculateTotalPrice = (actual, discount, selectedServices) => {
    const serviceCount = selectedServices.length || 0;
    const platformFee = serviceCount * 99;

    let totalActual = actual ? Number(actual) + platformFee : "";
    let totalDiscount = discount ? Number(discount) + platformFee : "";
    let total = totalDiscount || totalActual;

    return {
      adjustedActualPrice: totalActual,
      adjustedDiscountPrice: totalDiscount,
      totalPrice: total,
      platformFee: platformFee,
    };
  };

  if (loading) {
    return (
      <div className="dashboard-body__content">
        <div className="text-center py-5">Loading package details...</div>
      </div>
    );
  }



  return (
    <div className="dashboard-body__content">
      <div className="card common-card border border-gray-five">
        <div className="card-body">
          <h4 className="mb-4">Edit Package</h4>
          {status !== "Approved" && (
            <p className="text-danger">
              Please wait for admin approval to start listing your package.
            </p>
          )}
          <form onSubmit={handleSubmit}>
            <div className="row gy-4">
              {/* Package Name */}
              <div className="col-sm-12">
                <label className="form-label">
                  Package Name <span className="text-danger">*</span>
                </label>
                <input
                  type="text"
                  name="name"
                  value={packageData.name}
                  onChange={handleInputChange}
                  className="form-control"
                  placeholder="Enter Package Name"
                  required
                />
              </div>

                <div className="col-sm-12">
                <label className="form-label">
                  Select Services <span className="text-danger">*</span>
                </label>
                <Select
                  isMulti
                  isDisabled={status !== "Approved"}
                  options={services}
                  value={packageData.selectedServices}
                  onChange={handleServiceChange}
                  placeholder="Select services..."
                  menuPortalTarget={document.body}
                  styles={{
                    menuPortal: (base) => ({
                      ...base,
                      zIndex: 9999,
                    }),
                    menuList: (base) => ({
                      ...base,
                      maxHeight: "250px",
                      overflowY: "auto",
                    }),
                  }}
                  required
                />
              </div>

              {/* Price */}
              <div className="col-sm-4">
                <label className="form-label">
                  Actual Price<span className="text-danger">*</span>
                </label>
                <input
                  type="number"
                  name="actualPrice"
                  value={packageData.actualPrice}
                  onChange={handleChange}
                  className="form-control"
                  min="1"
                  placeholder="e.g. 2500"
                  required
                />
              </div>

              <div className="col-sm-4">
                <label className="form-label">Discount Price</label>
                <input
                  type="number"
                  name="discountPrice"
                  onChange={handleChange}
                  value={packageData.discountPrice}
                  className="form-control"
                  min="1"
                  placeholder="e.g. 500"
                />
              </div>

              <div className="col-sm-4">
                <label className="form-label">
                  Total Price <span className="text-danger">*</span>
                </label>
                <input
                  type="number"
                  name="totalPrice"
                  value={packageData.totalPrice}
                  onChange={handleChange}
                  className="form-control"
                  placeholder="e.g. 2000"
                  required
                />
              </div>

              {packageData.platformFee > 0 && (
                <p
                  style={{
                    marginTop: "8px",
                    fontStyle: "italic",
                    color: "#666",
                  }}
                >
                  ð¡ Platform Fee = {packageData.selectedServices.length}{" "}
                  services Ã â¹99 = â¹{packageData.platformFee}
                </p>
              )}

              {/* Doctors */}
              <div className="col-md-12">
                <label className="form-label">Doctor</label>
                <Select
                  isMulti
                  name="doctor"
                  value={selectedDoctorOptions}
                  onChange={handleDoctorChange}
                  options={doctorOptions}
                  placeholder="Select Doctor(s)..."
                  classNamePrefix="react-select"
                  menuPortalTarget={document.body}
                  styles={{
                    menuPortal: (base) => ({
                      ...base,
                      zIndex: 9999,
                    }),
                  }}
                  required
                />
              </div>

              {/* Time Slots */}
              <div className="col-md-12">
                <label className="form-label">Time Slots</label>

                {packageData?.shiftTimes?.map((slot, index) => (
                  <div key={index} className="row align-items-center mb-2 shift-row">
                    <div className="col-md-3">
                      <select
                        name="day"
                        className="form-control"
                        value={slot.day}
                        onChange={(e) =>
                          handleShiftTimeChange(index, "day", e.target.value)
                        }
                        required
                      >
                        <option value="" disabled>
                          Select a Day
                        </option>
                        <option value="Sunday">Sunday</option>
                        <option value="Monday">Monday</option>
                        <option value="Tuesday">Tuesday</option>
                        <option value="Wednesday">Wednesday</option>
                        <option value="Thursday">Thursday</option>
                        <option value="Friday">Friday</option>
                        <option value="Saturday">Saturday</option>
                      </select>
                    </div>
                    <div className="col-md-3">
                      <input
                        type="time"
                        value={slot.from}
                        onChange={(e) =>
                          handleShiftTimeChange(index, "from", e.target.value)
                        }
                        className="form-control"
                        placeholder="From Time"
                        required
                      />
                    </div>
                    <div className="col-md-3">
                      <input
                        type="time"
                        value={slot.to}
                        onChange={(e) =>
                          handleShiftTimeChange(index, "to", e.target.value)
                        }
                        className="form-control"
                        placeholder="To Time"
                        required
                      />
                    </div>
                    <div className="col-md-2">
                      <button
                        type="button"
                        className="btn btn-danger"
                        onClick={() => removeShiftTime(index)}
                        disabled={packageData.shiftTimes.length === 1}
                      >
                        Remove
                      </button>
                    </div>
                  </div>
                ))}

                <button
                  type="button"
                  className="btn btn-success mt-2"
                  onClick={addShiftTime}
                >
                  + Add Shift Time
                </button>
              </div>

              {/* Services */}
            

              {/* Reporting Time */}
              <div className="col-md-12">
                <label className="form-label">
                  Report Time Availability<span className="text-danger">*</span>
                </label>
                <input
                  type="text"
                  name="reportingTime"
                  value={packageData.reportingTime}
                  className="form-control"
                  onChange={handleInputChange}
                  placeholder="Write a note"
                  required
                />
                <small className="form-text text-muted">
                  Example: You will get your report within 2 hours after
                  checkup.
                </small>
              </div>

              {/* Description */}
              <div className="col-sm-12">
                <label className="form-label">Description</label>
                <textarea
                  name="description"
                  value={packageData.description}
                  onChange={handleInputChange}
                  className="form-control"
                  placeholder="Enter Package Description"
                  rows={3}
                  required
                ></textarea>
              </div>

              {/* Submit */}
              <div className="col-sm-12 ">
                <button
                  type="submit"
                  className="btn btn-main btn-lg pill mt-3"
                  disabled={loading}
                >
                  {loading ? "Updating..." : "Update Package"}
                </button>
              </div>
            </div>
          </form>
        </div>

                 {confirmModal && (
  <div
    className="modal-backdrop"
    style={{
      position: "fixed",
      top: 0,
      left: 0,
      width: "100%",
      height: "100%",
      backgroundColor: "rgba(0,0,0,0.5)",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      zIndex: 9999,
    }}
  >
    <div
      className="modal-content"
      style={{
        backgroundColor: "white",
        padding: "20px",
        borderRadius: "8px",
        width: "600px",
        textAlign: "center",
      }}
    >
      <h5>Please verify the details you filled</h5>
      <p>Do you want to update the package?</p>
      <div className="mt-3" style={{ display: "flex", justifyContent: "center",gap:"10px" }}>
        <button
          className="btn btn-success"
          onClick={confirmSaveService}
        >
          Yes
        </button>
        <button
          className="btn btn-danger"
          onClick={() => setConfirmModal(false)}
        >
          No
        </button>
      </div>
    </div>
  </div>
)}

      </div>
          <style>
        {
          `@media (max-width: 576px) {
  .shift-row {
    gap: 10px; /* vertical spacing between inputs */
  }
}

  @media (max-width: 625px) {
    /* Make the card full width with some padding */
    .modal-backdrop {
     
      margin: 12px auto !important;
      padding: 15px !important;
    }

    .shift-row {
      flex-direction: column !important;
      gap: 10px;
    }

    .col-md-4,
    .col-md-6,
    .col-md-12 {
      flex: 0 0 100% !important;
      max-width: 100% !important;
    }

    .btn {
     
      margin-top: 5px !important;
    }

    /* Optional: reduce font size on small screens */
    .card-body {
      font-size: 14px;
    }
  }
          `
        }
      </style>
      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
};

export default EditPackageLayer;
